var that;
export default {
	name: "mine",
	data() {
		return {
			//../home/businesshome ../mine/sjmb ../home/orderpage ../home/paysuccess
			tuchaoAppid:'wx8abaf00ee8c3202e',
			extraData:{},
			modalName:null,
			avatarUrl:'',
			nickname:'',
			modalMPShow: 0,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			changygn:[
				{
					cuIcon: 'calendar',
					color: 'olive',
					name: '我的挂单',
					url:'../mine/publishbid',
					sessionFrom: '',
				},{
					cuIcon: 'moneybag',
					color: 'red',
					name: '我是商家',
					url:'../home/businesshome',
					sessionFrom: '',
				},{
					cuIcon: 'scan',
					color: 'black',
					name: '扫码核销',
					url:'smhx',
					sessionFrom: '',
				},{
					cuIcon: 'service',
					color: 'orange',
					name: '客服热线',
					url:'4000851667',
					sessionFrom: 'phone',
				},{
					cuIcon: 'message',
					color: 'cyan',
					name: '在线客服',
					url:'1',
					sessionFrom: 'message',
				},{
					cuIcon: 'more',
					color: 'red',
					name: '更多精彩',
					url:'2',
					sessionFrom: 'more',
				}
				],
			sellerFuns: [],
			otherFuns: [],
			scanTaskResult:{
				'vc_id' : '',
                'tp_ordersn'    : '',
                'log_ordersn'   : '',
                'tp_id'         : 0,
                'tp_title'      : '',
                'tp_price'      : '',
                'seller_id'     : 0,
                'seller_title'  : '',
                'uid'           : 0,
                'nickname'      : '',
                'number'        : '',
                'vc_status'     : 0,//核销成功 0 未使用 1 已核销
                'tp_status'     : 0,//服务完成 2 服务中 3服务完成
                'log_status'    : 0,//支付状态 0 未支付 1 已支付
				'vc_use_time'	:''
			}
		}
	},
	mounted() {
		that=this;
		// uni.pageScrollTo({scrollTop: 0})
		uni.setNavigationBarColor({frontColor: '#ffffff',backgroundColor: '#182E6C'})
		this.avatarUrl=uni.getStorageSync("headimg")
		this.nickname=uni.getStorageSync("nickname")
	},
	methods: {
		showModalMP(){
			// console.log(that.modalMPShow);
			if(that.modalMPShow == 1)
			{
				that.modalMPShow = 0;
			}
			else{
				that.modalMPShow = 1;
			}
			console.log(that.modalMPShow);
		},
		getUserInfo(res){
			// console.log(res)
			uni.setStorageSync('avatarUrl',res.detail.userInfo.avatarUrl)
		},
		navigateTo(url) {
			if(url=='4000851667'){
				uni.makePhoneCall({
					phoneNumber:url
				})
				return;
			}
			if(url=='smhx'){//扫码核销
				uni.scanCode({
					success(res) {
						// console.log(res.result);
						let data={vc_id:res.result};
						that.scanTask(data)
					},
					fail(res) {
						
					}
				})
				return;
			}
			else if(url == 'goMP')
			{
				that.showModalMP();
				// that.$api.msg('更多功能请至公众号体验')
				return;
			}
			uni.navigateTo({
				url:url
			})
		},
		scanTask(code){
			that.$api.postWithData(that.api.scanTask,code,
				function callbacks(res){
					if(res.code==1){
						that.scanTaskResult=res.data
						that.modalName='Modal'
						that.$api.msg(res.msg)
					}else{
						that.$api.msg(res.msg)
					}
			})
		}
	},
}
